.list-num {
  counter-reset: item;
}
.list-num li {
  list-style-type: none;
  counter-increment: item;
  margin-bottom: 13px;
  position: relative;
  padding-left: 35px;
}
.list-num li:before {
  content: counter(item);
  background-color: #002ea6;
  position: absolute;
  left: 0;
  top: 2px;
  width: 26px;
  height: 26px;
  text-align: center;
  border-radius: 50%;
  font-family: "Libre Franklin", sans-serif;
  color: #fff;
  font-size: 13px;
  font-style: normal;
  font-weight: 600;
  line-height: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.disclaimer {
  font-family: "Libre Franklin", sans-serif;
  color: #002ea6;
  font-size: 12px;
  font-style: normal;
  letter-spacing: 0.6px;
  font-weight: 400;
  line-height: 20px;
}
.disclaimer a[href^="tel:"]:not(.btn-default):not(.contact-section-btn) {
  color: #002ea6 !important;
  font-weight: bold;
  text-decoration: underline;
}
.disclaimer a[href^="tel:"]:not(.btn-default):not(.contact-section-btn):hover, .disclaimer a[href^="tel:"]:not(.btn-default):not(.contact-section-btn):focus {
